.archive-view{
    background-color: #eee;
    
    .post,
    .page{
        position: relative;
        background: white;
    }
    
    .post__content{
        padding: 2em 2em 3em;
    }
    
    @media screen and (min-width: $query__medium){
        &.has-sidebar{
            .site-content{
                display: flex;
                justify-content: space-between;
                flex-wrap: wrap;
            }
            
            .page-header{
                width: 100%;
                flex: 1 0 100%;
            }
            
            .content-area{
                width: $size__ratio-large;
            }
            
            #primary.widget-area{
                width: $size__ratio-small;
            }
        }
    }
}

.page-header{
    font-family: $font__sans;
}

.page.title{
    margin: 0 0 .5em;
}

.archive-description{
    margin: -1em 0 2em;
}

/* For content-none.php */
.search,
.error-404 {
	
	.page-content {
		margin-bottom: 2em;
		padding: 1em 2em;
		font-family: $font__sans;
		color: white;
		background: $color__interactive;
	}
	
	input[type="search"] {
		padding: .3em;
	}
	input[type="submit"] {
		border-color: white;
		color: white;
		
		&:focus,
		&:hover {
			color: black;
		}
	}
}

.sticky:before {
    content: '';
    display: block;
    height: 2em;
    width: 2em;
    position: absolute;
    top: 0;
    right: 0;
    border-width: 0 2em 2em 0;
    border-color: #eee #eee #fff;
    border-style: solid;
    box-shadow: -1px 2px 2px hsla(0, 0%, 0%, .1);
    
}

.index-image{
    img{
        display: block;
        margin: 0 auto;
    }
    
    a{
        &:focus > img,
        &:hover > img{
            opacity: .8;
        }
    }
}

.continue-reading {
	position: absolute;
	right: 0;
	bottom: 0;
	font-family: $font__sans;
	a {
		display: block;
		padding: .5em 1em;
		font-size: 90%;
		text-decoration: none;
		background: hsl(0, 0%, 85%);
		&:focus,
		&:hover {
			color: white;
			background: $color__interactive;
		}
	}
}